import styled from 'styled-components';

export const ContactSection = styled.div `
    padding: 50px 0;
    text-align: center;
`

export const ContactTitle = styled.h2 `
    font-size: 60px;
    margin-bottom: 30px;
    @media (max-width:575px) {
        font-size: 40px
    }
`

export const ConatactTitleSpan = styled.span `
    font-weight: normal
`

export const ContactForm = styled.form `
    width: 70%;
    margin: auto;
    @media (max-width:575px) {
        width: 90%
    }
`

export const Input = styled.input `
    box-sizing: border-box;
    outline: 0;
    padding: 5px;
    margin-bottom: 10px;
`

export const FormDiv = styled.div `
    overflow: hidden
`

export const DivInput = styled(Input) `
    width: 49%;
    float: ${ props => props.type === "text" ? 'left' : 'right' };
    @media (max-width:575px) {
        width: 100%;
    }
`

// .drop form .form-input input[type=text] {
//     float: left
// }

// .drop form .form-input input[type=email] {
//     float: right
// }

export const SubInput = styled(Input) `
    width: 100%;
`

export const TextArea = styled.textarea `
    width: 100%;
    outline: 0;
    background: #ccc
`

export const Submit = styled(Input) `
    width: 60%;
    background: #fff;
    border: 1px solid #ccc;
    color: #888;
    cursor: pointer;
`
